@import url(https://fonts.googleapis.com/css?family=Nunito&display=swap);
@import url(https://cdnjs.cloudflare.com/ajax/libs/font-awesome/5.13.0/css/all.min.css);
@import url(https://stackpath.bootstrapcdn.com/bootstrap/4.4.1/css/bootstrap.min.css);

*{

  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Nunito', sans-serif;

}

.title{
  text-align: center;
  margin-bottom: 50px;
  margin-top: 70px;
}

.title-text{
  text-align: center;
  color: rgb(93, 93, 93);
  position: relative;
  display: inline;
  margin: auto;
  font-weight: bold;
  font-size: 2em;

}

.title-text::before{
  content: '';
  position: absolute;
  bottom: 0;
  right: 0;
  left: 0;
  background-color: #0A79DF;
  height: 3px;
}

.fa-facebook-square{
  color: #3B5998;
}
.fa-twitter{
  color: #00ACEE;
}
.fa-instagram{
background: linear-gradient(#f58529 0%, #feda77 27.28%, #dd2a7b 65.49%, #8134af 86.22%, #515bd4 100%);
-webkit-background-clip: text;
-webkit-text-fill-color: transparent;
}
#Hero{height:100vh;position:relative;overflow:hidden}#Hero .bg{position:absolute;height:100%;left:-550px;z-index:-99}#Hero h1{color:#fff}#Hero .content{height:100%;display:flex;justify-content:center;align-items:flex-end;padding-bottom:50px;color:#fff;text-align:center}#Hero .content .left{padding-bottom:50px}#Hero .content .left h3{font-size:20px;opacity:0.8}#Hero .content .left h1{font-size:50px;margin-top:10px}#Hero .content .left .call-to-action{background:linear-gradient(#FF362E, #E8290B);padding:10px 20px;color:#fff;text-decoration:none;border-radius:5px;display:block;margin-top:30px}#Works .content{display:grid;grid-template-columns:1fr;grid-gap:10px;gap:10px}#Works .content .card{margin:10px;background-color:#fff;box-shadow:rgba(0,0,0,0.233) 3px 3px 8px;border-radius:5px}#Works .content .card img{width:100%}#Works .content .card p{margin:10px}#Connect .social{width:80%;margin:auto;display:flex;justify-content:space-around}#Connect .social .icons-btn i{font-size:3.3em}#Footer{background:linear-gradient(#0a3d62 0%, #192a56 100%);padding:50px 10px;display:flex;justify-content:center;align-items:center}#Footer p{color:#fff;margin:0}#Footer p span{background:#3c40c6;padding:5px 10px;border-radius:50px}#Footer p span a{color:#fff;text-decoration:none}

